import React from "react";
import { render } from "react-dom";
import "./Button";

class Button extends React.Component {
  constructor() {
    super();
    this.state = { background: "transparent", color: "black" };
    this.style = {
      paddingTop: "15px",
      paddingBottom: "15px",
      paddingLeft: "50px",
      paddingRight: "50px",
      fontSize: "16px",
      borderColor: "black",
      background: "transparent",
      color: "black",
    };
  }
  render() {
    return (
      <button
        id="contactButton"
        style={Object.assign({}, this.style, {
          background: this.state.background,
          color: this.state.color,
        })}
        onMouseEnter={() => {
          this.setState({ background: "black", color: "white" });
        }}
        onMouseLeave={() => {
          this.setState({ background: "transparent", color: "black" });
        }}
        onClick={() => {
          return this.props.link;
        }}
      >
        Get in touch
      </button>
    );
  }
}

export default Button;
